@import url(https://use.typekit.net/hji6emp.css);
@import url(https://use.typekit.net/hji6emp.css);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Merriweather", "sans-serif";
}

p {
  font-family: proxima-nova, sans-serif;
  opacity: 0.7;
  line-height: 1.5rem;
}

.app-container {
  margin: 80px;
  padding: 100px;
  border: 2px solid #9e9e9e;
  background-color: #fafafa;
}

.main-content {
  width: 70%;
  margin: 0 auto;
  margin-top: 82px;
  margin-bottom: 82px;
}

@media screen and (max-width: 991px) {
  .app-container {
    margin: 0;
  }
}

.nav-wrapper {
  height: 350px;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-between;
}

.brand-top {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.logo-img {
  border: transparent;
  border-radius: 50%;
  margin: 50px 0 35px 0;
  height: 150px;
  width: auto;
}

.name-title {
  font-family: proxima-nova, sans-serif;
  color: #000000;
  opacity: 55%;
  text-transform: uppercase;
  padding: 0 17px 17px 17px;
}
.menu-icon {
  display: none;
}

.navbar {
  width: 100%;
}

.navbar-container {
  width: 100%;
}
.nav-menu {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  max-width: 355px;
  margin: 0 auto;
}
.nav-links {
  color: #000000;
  opacity: 50%;
  text-decoration: none;
  display: inline-block;
  font-family: proxima-nova, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}

.nav-links:hover {
  opacity: 100%;
  cursor: cursor;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
}

@media screen and (max-width: 991px) {
}

@media screen and (max-width: 425px) {
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0px;
    left: -100%;
    opacity: 0;
    transition: all 0.7s ease;
    background-color: #fafafa;
    z-index: 999;
  }

  .nav-menu.active {
    top: 50px;
    left: 0;
    opacity: 1;
    transition: all 0.7s ease;
  }

  .nav-item {
    margin-bottom: 20px;
  }

  .nav-links {
    font-size: 20px;
  }
}

.dropdown-menu {
  background: red;
  width: 200px;
  position: absolute;
  top: 80px;
  list-style: none;
  text-align: start;
}

.dropdown-menu li {
  background: #1888ff;
  cursor: pointer;
}

.dropdown-menu li:hover {
  background: #5cabff;
}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff;
  padding: 16px;
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    display: none;
  }
}

.qna__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

.title h2 {
  font-weight: bold;
  font-family: Merriweather, sans-serif;
  font-size: 1rem;
  margin-bottom: 0.5rem;
  font-style: italic;
  opacity: 0.7;
}

.project__show {
  display: flex;
  width: 100%;
  object-fit: contain;
}

.project__show div {
  width: 200px;
  height: 200px;
}
.project__show img {
  object-fit: contain;
  max-width: 100%;
  height: auto;
}

